@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-color: black;
  color: white;
}

.c-cell {
  width: 100%;
  height: 100%;
}

.gallery {
  position: relative;
}

.gallery-cell {
  margin: 0.9rem;
  counter-increment: gallery-cell;
}

.navbar {
  background-color: #161f2f;
}

.margin-top-20 {
  margin-top: 20;
}

.w-65 {
  width: 65%;
}

.container {
  margin: 0 auto;
}

/* no circle */
.flickity-button {
  background: transparent;
  color: white;
  position: absolute;
  z-index: 3;
}

.flickity-button:hover {
  background: white;
  color: black;
}

.flickity-button:disabled {
  color: rgba(255, 255, 255, 0.589);
}

button.flickity-prev-next-button.previous {
  right: 60px !important;
  top: -30px;
  left: auto !important;
}

.flickity-prev-next-button.next {
  right: 10px;
  top: -30px;
}

.line {
  background-color: #c7950a;
  width: 25px;
  height: 2.3px;
}

.nowPlaying .gallery-cell {
  width: 90%;
  height: 22rem;
}

@media only screen and (min-width: 768px) {
  .nowPlaying .gallery-cell {
    width: 80%;
  }
}
.c-cell a {
  width: fit-content;
}

.forSearch {
  margin: 10px auto;
  width: 250px;
  height: 30px;
}

/* The point of this tutorial is here */
.form {
  display: flex;
  flex-direction: row;
}

.search-field {
  width: 300px;
  padding: 4px 60px 4px 10px;
  border: none;
  border-radius: 100px;
  outline: none;
  background-color: #2c3443;
}

.search-button {
  background: transparent;
  border: none;
  outline: none;
  margin-left: -33px;
}

.search-button img {
  width: 20px;
  height: 20px;
  object-fit: cover;
}

a img {
  height: 3rem;
  width: 3rem;
}

.card {
  font-size: 1.6666666667rem;
}

@media only screen and (min-width: 375px) {
  /* for tablets */
  .forimg1 {
    margin-right: 0;
  }
}
@media only screen and (min-width: 768px) {
  .whyChoose {
    margin-top: 0rem;
  }

  .forimg1 {
    margin-right: -7rem;
  }
}